.partnership {
  margin: 70px 0 30px 0;
  h2 {
    text-align: center;
    font-size: 1em;
    color: #34a853;
  }
  .partner {
    display: grid;
    justify-content: center;
    div {
      margin: 30px 0 20px 0;
      display: flex;
      justify-content: center;
    }
  }
  .skillPartner {
    background-color: #35a555;
    padding: 20px;
    h3 {
      text-align: center;
      color: #ffffff;
      font-size: 1.5em;
      line-height: 1.5em;
    }
    h4 {
      text-align: center;
      color: #80d2bb;
      font-weight: 100;
      line-height: 1.5em;
    }
  }
  .skillTech {
    display: flex;
    flex-direction: column;
    .skilled {
      display: flex;
    }
    .skills {
      width: 80%;
      margin: 10px;
      color: #f2ffff;
      p {
        font-size: 0.8em;
        line-height: 2em;
      }
    }
  }
}
@media only screen and (min-width: 440px) {
  .partnership {
    .partner {
      margin: 0 40px 0 40px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 5px;
    }
    .skillPartner {
      padding: 20px 30px;
    }
  }
}
@media only screen and (min-width: 769px) {
  .partnership {
    .partner {
      padding: 50px 10px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
    .skillPartner {
      padding: 30px 100px;
      text-align: center;
      h3 {
        font-size: 2.5em;
      }
      h4 {
        font-size: 1.8em;
      }
      .skillTech {
        .skilled {
          text-align: left;
          .skills {
            p {
              font-size: 1em;
            }
          }
        }
      }
    }
  }
}
@media only screen and (min-width: 1040px) {
  .partnership {
    .partner {
      padding: 20px 100px;
    }
    .skillPartner {
      padding: 20px 100px;
      .skillTech {
        display: grid;
        grid-template-columns: 1fr 1fr;
        .skilled {
          display: grid;
          grid-template-columns: 1fr 1fr;
        }
      }
    }
  }
}
