.header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  background: linear-gradient(rgba(3, 3, 3, 0.3), rgba(8, 8, 8, 0.3));
  width: 100%;
  margin: 0;
  padding: 20px;
  div {
    right: 0;
    img {
      height: 18px;
      margin-top: 10px;
      margin-right: 40px;
    }
    p {
      display: none;
    }
  }
}
@media only screen and (min-width: 440px) {
  .header {
    padding: 30px 60px;
    div {
      margin-right: 120px;
    }
  }
}
@media only screen and (min-width: 995px) {
  .header {
    div {
      margin-right: 200px;
      img {
        display: none;
      }
      p {
        display: block;
        margin-top: 20px;
        a {
          text-decoration: none;
          color: white;
        }
      }
    }
  }
}
@media only screen and (min-width: 1040px) {
  .header {
    padding: 20px 120px;
    div {
      margin-right: 150px;
    }
  }
}
