.hero {
  background: linear-gradient(rgba(3, 3, 3, 0.9), rgba(8, 8, 8, 0.7)),
    url("../images/hero.jpg");
  background-size: cover;
  background-position: bottom right;
  height: 100vh;
  width: 100%;
  color: #fff;
  text-align: center;
  padding-top: 100px;
  font-weight: bold;

  div {
    h1 {
      font-size: 2em;
      line-height: 1em;
      padding: 60px 50px 10px 50px;
      width: 100%;
      b {
        color: #b4d245;
      }
    }
    p {
      font-size: 1em;
      padding: 25px;
      line-height: 1.5em;
      text-align: center;
    }
  }
  button {
    background: linear-gradient(90deg, rgb(61, 171, 82), rgb(173, 208, 70));
    height: 40px;
    width: 50%;
    border: 1px solid transparent;
    border-radius: 3px;
    margin: 20px;
    color: #fff;
    font-weight: 500;
  }
}
@media only screen and (min-width: 440px) {
  .hero {
    width: 100%;
    div {
      padding: 20px 30px;
      p {
        line-height: 2em;
      }
    }
    button {
      height: 50px;
      width: 30%;
    }
  }
}
@media only screen and (min-width: 769px) {
  .hero {
    div {
      h1 {
        font-size: 3em;
      }
      p {
        font-size: 1.5em;
      }
    }
    button {
      height: 50px;
    }
  }
}
@media only screen and (min-width: 1040px) {
  .hero {
    width: 100%;
    div {
      h1 {
        font-size: 3.5em;
        padding: 40px 100px;
      }
      p {
        padding: 20px 100px;
      }
    }
    button {
      width: 20%;
      height: 60px;
    }
  }
}
