.footer {
  background-color: #2c3134;
  color: white;
  padding: 20px;
  .hire {
    text-align: center;
    border-bottom: 1px solid white;
    padding: 40px 20px 60px 20px;
    h2 {
      font-size: 2.5em;
      color: white;
    }
    button {
      background: linear-gradient(90deg, rgb(61, 171, 82), rgb(173, 208, 70));
      height: 40px;
      width: 60%;
      border: 1px solid transparent;
      border-radius: 3px;
      margin: 20px;
      color: #fff;
      font-weight: bold;
    }
  }
  footer {
    .footerAll {
      .infoLinksAll {
        .infoLinks {
          display: grid;
          grid-template-columns: 1fr 1fr;
          column-gap: 30px;
          font-size: 0.8em;
          line-height: 2em;
        }
        .contact {
          font-size: 0.8em;
          margin: 30px 0 0px 0;
          div {
            margin: 10px 0 10px 0;
            display: flex;
            p {
              margin-left: 40px;
              a {
                text-decoration: none;
                color: white;
              }
            }
          }
        }
      }
      .footerCenter {
        display: flex;
        text-align: center;
        justify-content: center;
        .footerInner {
          .footerIcons {
            img {
              margin: 20px 10px;
            }
          }
          p {
            font-size: 0.8em;
            a {
              text-decoration: none;
              color: #35a555;
            }
          }
        }
      }
    }
  }
}
@media only screen and (min-width: 769px) {
  .footer {
    padding: 40px;
    .hire {
      h2 {
        font-size: 3em;
        margin: 30px 10px;
      }
      button {
        width: 35%;
        height: 65px;
      }
    }
    footer {
      .footerAll {
        padding: 30px 20px;
        .infoLinksAll {
          p {
            line-height: 2.5em;
          }
        }
      }
    }
  }
}
@media only screen and (min-width: 995px) {
  .footer {
    footer {
      .footerAll {
        display: flex;
        .infoLinksAll {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 20px;
          .infoLinks {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 30px;
            width: 100%;
            div:first-child {
              width: 150px;
            }
          }
        }
        .footerCenter {
          width: 40%;
        }
      }
    }
  }
}
@media only screen and (min-width: 1023px) {
  .footer {
    footer {
      .footerAll {
        display: flex;
        flex-direction: row-reverse;
      }
    }
  }
}
@media only screen and (min-width: 1040px) {
  .footer {
    padding: 20px 80px;
    .hire {
      button {
        width: 20%;
      }
    }
    .footerAll {
      footer {
        .footerAll {
          .infoLinksAll {
            .infoLinks {
              div {
                p {
                  line-height: 2.5em;
                }
              }
            }
          }
        }
      }
    }
  }
}
