.LeadershipAll {
  background: linear-gradient(45deg, rgb(248, 250, 238), rgb(250, 251, 252));
  margin-top: -10px;
  p {
    font-size: 1em;
    line-height: 1.5em;
    color: gray;
  }
  .team {
    padding: 120px 20px 40px 20px;
    p {
      margin: 20px 0 20px 0;
    }
    img {
      width: 100%;
    }
  }
  .leadership {
    padding: 20px 20px 50px 20px;
    .ourLeaders {
      .lead {
        margin: 20px 0 20px 0;
        background-color: white;
        border-radius: 5px;
        padding: 40px;
        h3 {
          margin: 5px 0;
          color: rgb(58, 56, 56);
        }
        p {
          font-size: 0.8em;
        }
        ul {
          margin: 40px 0 20px 0;
          li {
            font-size: 0.8em;
            margin: 15px 0 15px 0;
            color: rgb(83, 80, 80);
          }
        }
        div {
          margin-top: 60px;
          display: flex;
          align-items: flex-end;
          p {
            margin: 10px 0 5px 25px;
          }
        }
      }
      .jason {
        .jas {
          width: 20%;
        }
      }
    }
  }
  .button {
    margin: 30px 0 30px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    button {
      color: gray;
      background-color: transparent;
      border: 1px solid rgb(224, 217, 217);
      width: 100%;
      height: 50px;
    }
  }
}
@media only screen and (min-width: 440px) {
  .LeadershipAll {
    padding: 20px 30px;
  }
}
@media only screen and (min-width: 769px) {
  .LeadershipAll {
    .team {
      width: 80%;
      .teamDescribe {
        p:first-child {
          font-size: 1.5em;
          color: rgb(34, 34, 34);
        }
      }
    }
    .leadership {
      h2 {
        font-size: 2em;
        text-align: center;
      }
      .ourLeaders {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 30px;
        .lead {
          h3 {
            font-size: 1.5em;
            margin: 20px 0 10px 0;
          }
          p {
            font-size: 1em;
          }
          ul {
            li {
              font-size: 0.9em;
            }
          }
        }
      }
      .button {
        display: none;
      }
    }
  }
}
@media only screen and (min-width: 995px) {
  .LeadershipAll {
    .team {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 60px;
      margin: 0;
      img {
        width: 100%;
      }
      div {
        padding-right: 30px;
        p {
          line-height: 1.2em;
        }
      }
    }
  }
}

@media only screen and (min-width: 1040px) {
  .LeadershipAll {
    padding: 20px 100px;
    .leadership {
      .ourLeaders {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 20px;
        .lead {
          margin-top: 2px;
        }
      }
    }
  }
}
