.reason {
  p {
    font-size: 1em;
    font-weight: lighter;
    line-height: 1.5em;
    color: gray;
  }
  margin: 10px 0 10px 0;
  .companyLogos {
    p {
      color: rgb(58, 56, 56);
      text-align: center;
      margin: 12px;
    }
    .reasonImage {
      // display: flex;
      img {
        margin: 10px 0 10px 0;
        width: 30%;
      }
    }
  }
  .WhyDec {
    margin: 120px 20px 40px 20px;
    img {
      width: 100%;
    }
    .myWorkspace {
      .workspace {
        margin: 50px 0 50px 0;
        div {
          margin: 20px 0 20px 0;
          display: flex;
          img {
            width: 10%;
          }
        }
      }
    }
  }
  .space {
    background-color: #f5f9e7;
    height: 10vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    .innerSpace {
      width: 80%;
      height: 2vh;
      background-color: white;
      position: absolute;
    }
  }
}
@media only screen and (min-width: 440px) {
  .reason {
    .companyLogos {
      padding: 0 30px;
      .reasonImage {
        img {
          width: 30%;
        }
      }
    }
    .WhyDec {
      padding: 5px 20px;
    }
  }
}
@media only screen and (min-width: 769px) {
  .reason {
    .companyLogos {
      .reasonImage {
        img {
          width: 20%;
        }
      }
    }
    .WhyDec {
      img {
        width: 80%;
      }
      .myWorkspace {
        display: grid;
        grid-template-columns: 1fr 1fr;
        .workspace {
          margin-top: 5px;
          padding: 5px;
          width: 90%;
          div {
            margin-top: 5px;
          }
          p {
            line-height: 2em;
          }
        }
      }
    }
  }
}
@media only screen and (min-width: 1023px) {
  .reason {
    .WhyDec {
      .myWhyDec {
        display: flex;
        img {
          width: 50%;
          height: 350px;
        }
        .myWorkspace {
          display: flex;
          flex-wrap: wrap;
          margin: 10px 50px;
          margin-top: -40px;
          .workspace {
            width: 100%;
            div {
              height: 50px;
              margin: 0;
              h2 {
                margin: 0;
              }
              img {
                width: 30px;
                height: 30px;
              }
            }
            p {
              line-height: 1.5em;
            }
          }
        }
      }
    }
  }
}
@media only screen and (min-width: 1040px) {
  .reason {
    .companyLogos {
      padding: 20px 100px;
      display: flex;
      justify-content: space-between;
      .reasonImage {
        display: flex;
        justify-content: space-between;
        img {
          width: 20%;
        }
      }
    }
    .WhyDec {
      padding: 20px 100px;
      .myWhyDec {
        img {
          width: 600px;
          height: 400px;
        }
        .myWorkspace {
          display: grid;
          grid: 1fr 1fr;
          .workspace {
            padding: 15px;
            div {
              margin-top: 25px;
            }
          }
        }
      }
    }
  }
}
