.news {
  background-color: #fbfbfc;
  padding: 20px 0 60px 0;
  .newsHeading {
    p {
      display: none;
    }
  }
  .allNews {
    .new {
      background-size: cover;
      height: 80vh;
      margin: 10px;
      display: flex;
      align-items: flex-end;
      color: white;
      div {
        margin: 20px;
        p {
          margin: 20px 0 20px 0;
        }
        p:first-child {
          font-size: 1.2em;
          font-weight: 400;
        }
        p:last-child {
          font-size: 0.8em;
        }
      }
    }
    .newOne {
      background: url("../images/MicrosoftTeams-image-2.jpg");
    }
    .newsTwo {
      background: url("../images/MicrosoftTeams-image-3.jpg");
    }
    .newsThree {
      background: url("../images/MicrosoftTeams-image-4.jpg");
    }
  }
}
@media only screen and (min-width: 440px) {
  .news {
    padding: 20px 30px;
  }
}
@media only screen and (min-width: 769px) {
  .news {
    .newsHeading {
      display: flex;
      justify-content: space-between;
      p {
        display: flex;
        align-items: center;
        a {
          text-decoration: none;
          color: #34a853;
        }
      }
    }
    .allNews {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
}
@media only screen and (min-width: 1040px) {
  .news {
    padding: 20px 100px;
    .allNews {
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}
